












































































































import { Vue, Component, Prop, Model, Watch, Emit } from "vue-property-decorator";
import Bus from "./Bus";
import Url from "../api";

@Component
export default class OrganizationInfo extends Vue {
  // 分页信息
  @Prop({ type: Object, required: true })
  pagination!: any;
  // 组织树
  organizationTree: Array<any> = [];
  // 选中的组织
  organization: any = null;
  // 组织树兄弟节点数据
  levelData: Array<any> = [];
  // 组织架构面包屑导航数据
  breadcrumbData: Array<any> = [];
  // 焦点
  currentIndex: any = "";

  created() {
    this.getOrganizationTree();
    this.adjustmentMethod();
  }
  /**
   * 获取组织机构树
   */
  getOrganizationTree() {
    this.$http(Url.organization.getOrgTreeData).then((response: any) => {
      if (response.data.status) {
        this.organizationTree = response.data.data;
        this.levelData = response.data.data;
      } else {
        this.$message.error(response.data.message);
      }
    });
  }
  /**
   * 组织选择（改变面包屑和下面展示信息）
   * @param data  当前树节点信息
   */
  organizationSelect(data: any) {
    if (this.levelData) {
      this.levelData.forEach((item: any) => {
        if (item.id === data.id && item.children.length) {
          this.levelData = item.children;
          this.breadcrumbData.push(data);
        }
      });
      this.organization = data;
      // this.currentIndex = data.id;
      this.adjustmentMethod(data);
      this.getList();
    }
  }
  /**
   * 清空面包屑数据
   */
  EmptyBreadcrumbsData() {
    this.breadcrumbData = [];
    this.levelData = this.organizationTree;
    // this.currentIndex = "";
    this.adjustmentMethod();
    this.organization = { id: "empty" };
    this.getList();
    // this.pagination.total = 0;
    // this.pagination.currentPage = 1;
    // this.pagination.tableData = [];
  }
  /**
   * 截取面包屑数据
   * @param data  当前树节点信息
   * @param index 需要截取的位置
   */
  sliceBreadcrumbsData(data: any, index: any) {
    this.breadcrumbData = this.breadcrumbData.slice(0, index + 1);
    this.levelData = data.children;
    this.organization = data;
    // this.currentIndex = data.id;
    this.adjustmentMethod(data);
    this.getList();
  }
  /**
   * 控制组织选中状态和地图绘制按钮显隐
   * @param data 有无组织被选中，值是ID
   */
  adjustmentMethod(data: any = null) {
    this.currentIndex = data;
    Bus.$emit("OrganizationInfo", "", data);
  }
  /**
   * 根据组织ID获取列表信息
   */
  @Emit("getList")
  getList() {
    return this.organization;
  }
  /**
   * 查看
   * @param row     要查看的数据
   * @param column  列数据（主要用于区分查看和编辑操作）
   */
  handleView = (row: any, column: any) => {
    if (column.label !== "操作") {
      Bus.$emit("OrganizationInfo", row, "view");
    }
  };
  handleSizeChange(val: number) {
    this.pagination.pageSize = val;
    this.getList();
  }
  /**
   * 编辑
   * @param row  要编辑的数据
   * @param sign 标志（添加、编辑或者删除，可用于弹窗标题显示）
   */
  handleEdit = (row: any, sign: string) => {
    Bus.$emit("OrganizationInfo", row, sign);
  };
  /**
   * 删除
   * @param row  要删除的数据
   * @param sign 标志（添加、编辑或者删除，可用于弹窗标题显示）
   */
  handleDelete(row: any, sign: string) {}
  // 查看列表详情
  @Emit("detailsView")
  detailsView() {
    return this.currentIndex;
  }
}
