







import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import Bus from "./Bus";

const AMap: any = (window as any).AMap;

@Component
export default class GeographicMap extends Vue {
  // 按钮名称
  @Prop({ type: String, default: "绘制" })
  buttonName!: string;
  // 按钮状态
  buttonStatus: any = null;
  // 地图样式相关
  map: any;
  // 鼠标工具
  mouseTool: any = null;
  center: any = [106.569137, 29.643814];
  zoom: number = 22;
  mapStyle: string = "amap://styles/7a2258c5592a544ff4242a2d0341ef6c";
  // 多边形相关
  polygonRelated: any = {
    // 多边形编辑，用于关闭编辑
    polyEditor: null,
    // 将要显示的网格存起来，用于监听事件命名，方便去除
    polygons: [],
    // 用于记录上一次的多边形，便于清除
    polygon: null
  };
  // 折线相关
  plylinep: any = {};
  // form表单
  messageForm: any = {};

  created() {
    Bus.$on("OrganizationInfo", (row: any, sign: string) => {
      switch (sign) {
        case "edit":
          this.polygonEdit(row);
          break;
        case "view":
          this.polygonView(row);
          break;
        default:
          this.buttonStatus = sign;
      }
    });
  }
  mounted() {
    this.initMap();
  }
  // 地图加载方法
  initMap() {
    this.map = Object.freeze(
      new AMap.Map("a_map", {
        center: this.center,
        zoom: this.zoom,
        viewMode: "3D",
        mapStyle: this.mapStyle,
        resizeEnable: true
      })
    );
    this.map.plugin(["AMap.MouseTool"]);
    this.map.plugin(["AMap.PolyEditor"]);
  }
  /**
   * 多边形展示（所有）
   * @param records
   */
  polygonShow(records: Array<any>) {
    this.map.clearMap();
    this.polygonRelated.polygons = records;
    records.forEach((item: any) => {
      let polygon = new AMap.Polygon({
        path: eval(item.coordinates),
        isOutline: true,
        borderWeight: 1,
        strokeColor: "#FF33FF",
        strokeWeight: 1,
        strokeOpacity: 0.2,
        fillOpacity: 0.4,
        fillColor: "#1791fc",
        zIndex: 50
      }).on("dblclick", () => {
        this.$emit("openForm", item, "查看");
      });
      // item.listener = AMap.event.addListener(polygon, "click", function () {
      //   alert(666)
      // });
      this.map.add(polygon);
    });
    this.map.setFitView();
  }
  // 多边形绘制
  polygonDraw() {
    let _this = this;
    _this.buttonStatus = false;
    // 使用鼠标工具，在地图上画多边形
    _this.mouseTool = new AMap.MouseTool(_this.map);
    _this.mouseTool.polygon();
    // 画图完成之后触发
    _this.mouseTool.on("draw", function(e: any) {
      let polygon = new AMap.Polygon({
        map: _this.map,
        path: eval(GeographicMap.getString(e.obj.getPath())), // 设置多边形边界路径
        strokeColor: "#FF33FF", // 线颜色
        strokeOpacity: 0.2, // 线透明度
        strokeWeight: 3, // 线宽
        fillColor: "#1791fc", // 填充色
        fillOpacity: 0.35, // 填充透明度
        zIndex: 99
      });
      //多边形编辑工具，使指定多边形可编辑
      _this.polygonRelated.polyEditor = new AMap.PolyEditor(_this.map, polygon);
      //打开编辑功能
      _this.polygonRelated.polyEditor.open();
      //编辑完成后触发
      _this.polygonRelated.polyEditor.on("end", function(event: any) {
        _this.messageForm.coordinates = GeographicMap.getString(event.target.getPath());
        _this.messageForm.gridArea = event.target.getArea();
        _this.map.remove(polygon);
      });
      //所绘制网格点击事件
      polygon.on("click", function() {
        // 判断新绘制的网格与以前网格是否有冲突
        let flag = false;
        _this.polygonRelated.polygons.forEach((item: any) => {
          if (
            AMap.GeometryUtil.isRingInRing(eval(item.coordinates), polygon.getPath()) ||
            AMap.GeometryUtil.doesRingRingIntersect(eval(item.coordinates), polygon.getPath())
          ) {
            flag = true;
          }
        });
        if (flag) {
          _this.$message.warning("网格有重复区域！！请重新编辑网格");
        } else {
          // 编辑完成
          _this.polygonRelated.polyEditor.close();
          _this.$emit("openForm", _this.messageForm, "添加");
          _this.buttonStatus = true;
        }
      });
      //关闭网格绘制功能
      _this.mouseTool.close(true);
    });
  }
  /**
   * 多边形查看（重点显示某一个）
   * @param data 要查看的数据
   */
  polygonView(data: any) {
    let _this = this;
    if (_this.polygonRelated.polygon) {
      _this.map.remove(_this.polygonRelated.polygon);
    }
    if (_this.polygonRelated.polyEditor) {
      _this.polygonRelated.polyEditor.close();
    }
    _this.polygonRelated.polygon = new AMap.Polygon({
      path: eval(data.coordinates),
      isOutline: true,
      borderWeight: 1,
      strokeColor: "#FF33FF",
      strokeWeight: 1,
      strokeOpacity: 0.2,
      fillOpacity: 1,
      fillColor: "#FFD700",
      zIndex: 1
    });
    _this.map.add(_this.polygonRelated.polygon);
    _this.map.setFitView([_this.polygonRelated.polygon]);
  }
  /**
   * 多边形编辑
   * @param data 要编辑的数据
   */
  polygonEdit(data: any) {
    let _this = this;
    if (_this.polygonRelated.polygon) {
      _this.map.remove(_this.polygonRelated.polygon);
    }
    if (_this.polygonRelated.polyEditor) {
      _this.polygonRelated.polyEditor.close();
    }
    _this.polygonRelated.polygon = new AMap.Polygon({
      path: eval(data.coordinates),
      isOutline: true,
      borderWeight: 1,
      strokeColor: "#FF33FF",
      strokeWeight: 1,
      strokeOpacity: 0.2,
      fillOpacity: 0.4,
      fillColor: "#FFD700",
      zIndex: 99
    });
    _this.map.add(_this.polygonRelated.polygon);
    _this.map.setFitView([_this.polygonRelated.polygon]);
    // 多边形编辑工具，使指定多边形可编辑
    _this.polygonRelated.polyEditor = new AMap.PolyEditor(_this.map, _this.polygonRelated.polygon);
    // 打开编辑功能
    _this.polygonRelated.polyEditor.open();
    // 编辑完成后触发
    _this.polygonRelated.polyEditor.on("end", function(event: any) {
      _this.messageForm = data;
      _this.messageForm.area = event.target.getArea();
      _this.messageForm.coordinates = GeographicMap.getString(event.target.getPath());
      _this.map.remove(_this.polygonRelated.polygon);
    });
    _this.polygonRelated.polygon.on("click", () => {
      // 判断新绘制的网格与以前网格是否有冲突
      let flag = false;
      _this.polygonRelated.polygons.forEach((item: any) => {
        if (data.coordinates !== item.coordinates) {
          if (
            AMap.GeometryUtil.isRingInRing(eval(item.coordinates), _this.polygonRelated.polygon.getPath()) ||
            AMap.GeometryUtil.doesRingRingIntersect(eval(item.coordinates), _this.polygonRelated.polygon.getPath())
          ) {
            flag = true;
          }
        }
      });
      if (flag) {
        _this.$message.warning("网格有重复区域！！请重新编辑网格");
      } else {
        _this.polygonRelated.polyEditor.close();
        _this.$emit("openForm", _this.messageForm, "编辑");
      }
    });
  }
  //二维数组转字符串
  static getString(str: Array<any>) {
    let objArr = [];
    for (let i = 0; i < str.length; i++) {
      let str2 = [];
      str2.push(str[i].lng);
      str2.push(str[i].lat);
      objArr.push(str2);
    }
    let typeNO = objArr.length;
    let tree = "[";
    for (let i = 0; i < typeNO; i++) {
      tree += "[";
      tree += '"' + objArr[i][0] + '",';
      tree += '"' + objArr[i][1] + '"';
      tree += "]";
      if (i < typeNO - 1) {
        tree += ",";
      }
    }
    tree += "]";
    return tree;
  }

  beforeDestroy() {
    this.map.destroy();
    console.log(this.map);
  }
}
